/*! ridestyler.ui Docs */
// Core variables and mixins
@import "../../../src/variables";
@import "../../../src/mixins";

.version::after {
  content: "#{$version}";
}

.off-canvas {
  .off-canvas-toggle {
    font-size: 1rem;
    left: 1.5rem;
    position: fixed;
    top: 1rem;
  }

  .off-canvas-sidebar {
    width: 12rem;
  }

  .off-canvas-content {
    padding: 0;
  }
}

.docs-container {
  min-height: 100vh;
}

.docs-navbar {
  height: 3.8rem;
  position: fixed;
  right: 0;
  top: 0;
  z-index: $zindex-1;

  .btns {
    position: absolute;
    right: 1.5rem;
    top: 1rem;
    width: 14rem;
  }

  .algolia-autocomplete {
    flex: 1 1 auto;
  }
}

.docs-sidebar {
  .docs-nav {
    bottom: 1.5rem;
    overflow-y: auto;
    padding: .5rem 1.5rem;
    position: fixed;
    top: 3.5rem;
    width: 12rem;
    -webkit-overflow-scrolling: touch;
  }

  .accordion {
    margin-bottom: .75rem;

    input {
      & ~ .accordion-header {
        // color: lighten($dark-color, 10%);
      }
      
      &:checked {
        & ~ .accordion-header {
          // color: lighten($dark-color, 15%);
        }
      }
    }

    .menu {
      .menu-item {
        // font-size: .7rem;
        // padding-left: 1rem;
        
        & > a {
          background: transparent;
          // color: $gray-color-dark;
          display: inline-block;
        }
      }
    }
  }
}

.docs-content {
  flex: 1 1 auto;
  padding: 0 4rem;
  width: calc(100vw - 12rem);

  & > .container {
    margin-left: 0;
    max-width: 800px;
    padding-bottom: 1.5rem;
  }

  .anchor {
    color: $primary-color-light;
    display: none;
    margin-left: .2rem;
    padding: 0 .2rem;
    &:focus,
    &:hover {
      display: inline;
      text-decoration: none;
    }
  }
  
  .s-title,
  .s-subtitle {
    line-height: 1.8rem;
    margin-bottom: 0;
    padding-bottom: 1rem;
    padding-top: 1rem;
    position: static;

    @supports (position: sticky) {
      position: sticky;
      top: 0;
      z-index: $zindex-1 - 1;

      &::before {
        background: $light-color;
        bottom: 0;
        content: "";
        display: block;
        left: -10px;
        position: absolute;
        right: -10px;
        top: -5px;
        z-index: -$zindex-0;
      }
    }

    &:hover {
      .anchor {
        display: inline;
      }
    }

    & + .docs-note {
      margin-top: .4rem;
    }
  }

  .docs-demo {
    padding-bottom: 1rem;
    padding-top: 1rem;

    .card {
      box-shadow: 0 .25rem 1rem rgba($dark-color, .15);
      border: 0;
      height: 100%;
    }

    .column {
      margin-bottom: $unit-2;
    }
  }

  .docs-block {
    border-radius: $border-radius;
    padding: $unit-2;

    &.bg-gray {
      background: $bg-color-dark;
    }
  }

  .docs-shape {
    height: 4.8rem;
    line-height: 1.2rem;
    padding: 1.8rem 0;
    width: 4.8rem;
  }

  .docs-dot {
    border-radius: 50%;
    display: inline-block;
    height: .5rem;
    padding: 0;
    width: .5rem;
  }

  .docs-table {
    th,
    td {
      padding: .75rem .25rem;
    }
  }

  .docs-color {
    border-radius: $border-radius;
    margin: .25rem 0;
    padding: 5rem .5rem .5rem;

    .color-subtitle {
      font-size: $font-size-sm;
      opacity: .75;
    }
  }

  .code {
    .hljs-tag {
      color: lighten($body-font-color, 10%);
    }

    .hljs-comment {
      color: $gray-color;
    }

    .hljs-title,
    .hljs-class,
    .hljs-string,
    .hljs-number {
      color: $primary-color;
    }

    .hljs-name,
    .hljs-attribute,
    .hljs-variable,
    .hljs-keyword,
    .hljs-built_in {
      color: $code-color;
    }

    .hljs-value,
    .hljs-hexcolor {
      color: lighten($body-font-color, 10%);
    }
  }

  .c-select-all {
    user-select: all;
  }

  .panel {
    height: 75vh;

    .tile {
      margin: .75rem 0;
    }
  }

  .parallax {
    margin: 2rem auto;
  }

  .form-autocomplete .menu {
    position: static;
  }

  .example-tile-icon {
    align-content: space-around;
    align-items: center;
    background: $primary-color;
    border-radius: $border-radius;
    color: $light-color;
    display: flex;
    font-size: 1.2rem;
    height: 2rem;
    width: 2rem;

    .icon {
      margin: auto;
    }
  }

  .comparison-slider {
    height: auto;
    padding-bottom: 56.2222%;

    .filter-grayscale {
      filter: grayscale(75%);
    }
  }

  .off-canvas {
    position: relative;

    .off-canvas-toggle {
      left: $layout-spacing;
      position: absolute;
      top: $layout-spacing;
      z-index: $zindex-0;
    }
  }
}

.docs-brand {
  color: $primary-color;
  height: 2rem;
  left: 1.5rem;
  position: fixed;
  top: .85rem;

  .docs-logo {
    align-items: center;
    border-radius: $border-radius;
    display: inline-flex;
    font-size: .7rem;
    height: 2rem;
    padding: .2rem;
    width: auto;
    
    &:focus,
    &:hover {
      text-decoration: none;
    }
  
    img {
      display: inline-block;
      height: auto;
      width: 1.6rem;
    }
  
    h2 {
      display: inline-block;
      font-size: .8rem;
      font-weight: 700;
      line-height: 1.5rem;
      margin-bottom: 0;
      margin-left: .5rem;
      margin-right: .3rem;
    }
  }
}

.docs-footer {
  color: $gray-color;
  padding: .5rem;

  a {
    color: $dark-color;
  }

  .icon-brain {
    color: pink;
  }
}

.docs-ad {
  background: $bg-color;
  border-radius: $border-radius;
  display: block;
  margin: 2rem 0;
  padding: $layout-spacing;
  position: relative;

  ins {
    line-height: 1;
    vertical-align: middle;
  }

  .carbon-wrap {
    .carbon-img {
      display: block;
    }
  
    .carbon-text {
      color: $gray-color-dark;
      display: block;
      font-size: $font-size-sm;
    }
  }
  
  .carbon-poweredby {
    color: $gray-color;
    font-size: $font-size-sm;
  }
}

/* Spectre Homepage style */
.section-hero {
  padding: 1rem .5rem;

  .docs-brand {
    position: absolute;
    top: .85rem;

    h2 {
      color: $primary-color;
    }
  }
}

.section-ads {
  padding: 1rem .5rem;
}

.section-updates {
  padding: 4.5rem .5rem 2.5rem .5rem;

  .card {
    border: 0;
    margin-bottom: 1rem;
  }
}

.section-features {
  padding: 4.5rem .5rem;

  .column {
    padding: $layout-spacing;
  }
}

.section-footer {
  color: $secondary-color-dark;
  padding: 1.8rem .75rem 1rem;
  position: relative;
  z-index: 200;

  a {
    color: $dark-color;
  }
}

.grid-hero {
  padding-bottom: 2rem;
  padding-top: 6rem;

  h1 {
    font-size: 1.6rem;
    font-weight: 600;
    margin-bottom: 1.5rem;
  }

  h2 {
    font-size: .9rem;
    font-weight: 400;
    line-height: 1.5rem;
    margin-bottom: 1.5rem;

    u {
      border-bottom: $border-width-lg solid currentColor;
      padding-bottom: $unit-o;
      text-decoration: none;
    }
  }

  .card {
    background: none;
    border: 0;
    padding: .5rem;

    .card-title {
      font-size: 1rem;
      font-weight: 700;
      margin-bottom: 0;
    }
  }
}

@media (max-width: 960px) {
  .off-canvas {
    .off-canvas-toggle {
      z-index: $zindex-3;
    }

    .off-canvas-content {
      width: 100%;
    }
  }

  .docs-sidebar {
    .docs-brand {
      margin: .85rem 1.5rem;
      padding: 0;
      position: static;
    }

    .docs-nav {
      margin-top: 1rem;
      position: static;
    }

    .menu .menu-item > a {
      padding: .3rem .4rem;
    }
  }

  .docs-navbar {
    backdrop-filter: blur(5px);
    background: rgba($bg-color, .65);
    left: 0;
  }

  .docs-content {
    min-width: auto;
    padding: 0 1.5rem;
    width: 100%;

    .s-title,
    .s-subtitle {
      padding-top: 5rem;
      position: static;

      &::before {
        content: none;
      }
    }
  }

  .section-hero {
    .s-brand {
      height: 5rem;
      padding: 1.5rem .5rem;
      text-align: center;
      width: 100%;

      .s-logo {
        height: auto;
      }

      img {
        height: 3.2rem;
        width: 3.2rem;
      }

      h2 {
        display: none;
      }
    }
  }
}

@media (max-width: 600px) {
  .grid-hero {
    h2 {
      font-size: .9rem;
    }
    .card {
      padding: 0;
    }
  }
  
  .off-canvas {
    .off-canvas-toggle {
      left: .5rem;
    }
  }

  .docs-navbar {
    .btns {
      right: .9rem;
    }
  }

  .grid-hero {
    .docs-brand {
      left: .9rem;
    }
  }

  .docs-sidebar {
    .docs-brand {
      margin: .85rem 1rem;
    }
  
    .docs-nav {
      padding: .5rem 1rem;
    }
  }

  .docs-content {
    padding: 0 .5rem;

    .docs-block {
      padding: $unit-2 $unit-h;
    }
  }
}

@media (min-width: 1366px) {
  .docs-ad {
    &.docs-ad-sidebar {
      bottom: 1rem;
      margin: 0;
      position: fixed;
      right: 1rem;
      width: 7.3rem;
    }
  }
}

// Docs search
.searchbox {
  display: inline-block;
  position: relative;
  width: 200px;
  height: 32px!important;
  white-space: nowrap;
  box-sizing: border-box;
  visibility: visible!important;

  .algolia-autocomplete {
    display:block;
    width:100%;
    height:100%;
  }
}

.searchbox__wrapper {
  width: 100%;
  height: 100%;
  z-index: 999;
  position: relative;
}
.searchbox__input {
  display: inline-block;
  box-sizing: border-box;
  transition: box-shadow .4s ease, background .4s ease;
  border: 0;
  border-radius: 16px;
  box-shadow: inset 0 0 0 1px #ccc;
  background: #fff !important;
  padding: 0 26px 0 32px;
  width: 100%;
  height: 100%;
  vertical-align: middle;
  white-space: normal;
  font-size: 12px;
  appearance: none;

  &::-webkit-search-cancel-button,
  &::-webkit-search-decoration,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

  &:hover {
    box-shadow: inset 0 0 0 1px #b3b3b3;
  }

  &:active,
  &:focus {
    outline: 0;
    box-shadow: inset 0 0 0 1px #aaa;
    background: #fff;
  }

  &::placeholder {
    color: #aaa;
  }
}

.searchbox__submit {
  position: absolute;
  top: 0;
  margin: 0;
  border: 0;
  border-radius: 16px 0 0 16px;
  background-color: rgba(69,142,225,0);
  padding: 0;
  width: 32px;
  height: 100%;
  vertical-align: middle;
  text-align: center;
  font-size: inherit;
  user-select: none;
  right: inherit;
  left: 0;

  &:before {
    display: inline-block;
    margin-right: -4px;
    height: 100%;
    vertical-align: middle;
    content: "";
  }

  &:active,
  &:hover {
    cursor: pointer;
  }

  &:focus {
    outline: 0;
  }

  svg {
    width: 14px;
    height: 14px;
    vertical-align: middle;
    fill: #6d7e96;
  }
}

.searchbox__reset {
  display: block;
  position: absolute;
  top: 8px;
  right: 8px;
  margin: 0;
  border: 0;
  background: none;
  cursor: pointer;
  padding: 0;
  font-size: inherit;
  user-select: none;
  fill: rgba(0,0,0,.5);

  &.hide {
    display: none;
  }

  &:focus {
    outline: 0;
  }

  svg {
    display: block;
    margin: 4px;
    width: 8px;
    height: 8px;
  }
}


.searchbox__input:valid ~ .searchbox__reset {
  display: block;
  animation-name: sbx-reset-in;
  animation-duration: .15s;
}

@keyframes sbx-reset-in {
  0%{
      transform: translate3d(-20%, 0, 0);
      opacity: 0;
  }
  to{
      transform: none;
      opacity: 1;
  }
}

.algolia-autocomplete {
  &.algolia-autocomplete-right .ds-dropdown-menu {
    right: 0 !important;
    left: inherit !important;
    &:before {
      right: 48px;
    }
  }

  &.algolia-autocomplete-left .ds-dropdown-menu {
    left: 0 !important;
    right: inherit !important;
    &:before {
      left: 48px;
    }
  }

  .ds-dropdown-menu {
    top: -6px;
    border-radius: 4px;
    margin: 6px 0 0;
    padding: 0;
    text-align: left;
    height: auto;
    position: relative;
    background: transparent;
    border: none;
    z-index: 999;
    max-width: 600px;
    min-width: 500px;
    @include shadow-variant(.05rem);

    .ds-suggestions {
      position: relative;
      z-index: 1000;
      margin-top: 8px;
      a:hover {
        text-decoration: none;
      }
    }

    .ds-suggestion {
      cursor: pointer;
      &.ds-cursor .algolia-docsearch-suggestion {
        &.suggestion-layout-simple, &:not(.suggestion-layout-simple) .algolia-docsearch-suggestion--content {
          background-color: rgba($primary-color, .05);
        }
      }
    }

    [class^=ds-dataset-] {
      position: relative;
      background: #fff;
      border-radius: 4px;
      overflow: auto;
      padding: 0 8px 8px;
    }

    * {
      box-sizing: border-box;
    }
  }

  .algolia-docsearch-suggestion {
    display: block;
    position: relative;
    padding: 0 8px;
    background: #fff;
    color: $dark-color;
    overflow: hidden;
  }

  .algolia-docsearch-suggestion--highlight {
    color: $primary-color;
    background: rgba($primary-color, .1);
    padding: 0.1em 0.05em;
  }

  .algolia-docsearch-suggestion--category-header {
    .algolia-docsearch-suggestion--category-header-lvl0 .algolia-docsearch-suggestion--highlight, .algolia-docsearch-suggestion--category-header-lvl1 .algolia-docsearch-suggestion--highlight {
      padding: 0 0 1px;
      background: inherit;
      box-shadow: inset 0 -2px 0 0 rgba($primary-color, .8);
      color: inherit;
    }
  }

  .algolia-docsearch-suggestion--text .algolia-docsearch-suggestion--highlight {
    padding: 0 0 1px;
    background: inherit;
    box-shadow: inset 0 -2px 0 0 rgba($primary-color, .8);
    color: inherit;
  }

  .algolia-docsearch-suggestion--content {
    display: block;
    float: right;
    width: 70%;
    position: relative;
    padding: 5.33333px 0 5.33333px 10.66667px;
    cursor: pointer;
    &:before {
      content: "";
      position: absolute;
      display: block;
      top: 0;
      height: 100%;
      width: 1px;
      background: $gray-color-light;
      left: -1px;
    }
  }

  .algolia-docsearch-suggestion--category-header {
    position: relative;
    border-bottom: 1px solid $gray-color-light;
    display: none;
    margin-top: 8px;
    padding: 4px 0;
    font-size: 1em;
    color: $dark-color;
  }

  .algolia-docsearch-suggestion--wrapper {
    width: 100%;
    float: left;
    padding: 8px 0 0;
  }

  .algolia-docsearch-suggestion--subcategory-column {
    float: left;
    width: 30%;
    text-align: right;
    position: relative;
    padding: 5.33333px 10.66667px;
    color: $gray-color;
    font-size: .9em;
    word-wrap: break-word;
    &:before {
      content: "";
      position: absolute;
      display: block;
      top: 0;
      height: 100%;
      width: 1px;
      background: $gray-color-light;
      right: 0;
    }
  }

  .algolia-docsearch-suggestion--subcategory-inline {
    display: none;
  }

  .algolia-docsearch-suggestion--title {
    color: $dark-color;
    font-size: .9em;
    font-weight: 700;
  }

  .algolia-docsearch-suggestion--text {
    display: block;
    line-height: 1.2em;
    font-size: .85em;
    color: $gray-color-dark;
  }

  .algolia-docsearch-suggestion--no-results {
    width: 100%;
    padding: 8px 0;
    text-align: center;
    font-size: 1.2em;
    &:before {
      display: none;
    }
  }

  .algolia-docsearch-suggestion {
    code {
      padding: 1px 5px;
      font-size: 90%;
      border: none;
      color: #222;
      background-color: #ebebeb;
      border-radius: 3px;
      font-family: Menlo, Monaco, Consolas, Courier New, monospace;
      .algolia-docsearch-suggestion--highlight {
        background: none;
      }
    }

    &.algolia-docsearch-suggestion__main .algolia-docsearch-suggestion--category-header, &.algolia-docsearch-suggestion__secondary {
      display: block;
    }
  }

  .suggestion-layout-simple {
    &.algolia-docsearch-suggestion {
      border-bottom: 1px solid #eee;
      padding: 8px;
      margin: 0;
    }

    .algolia-docsearch-suggestion--content {
      width: 100%;
      padding: 0;
      &:before {
        display: none;
      }
    }

    .algolia-docsearch-suggestion--category-header {
      margin: 0;
      padding: 0;
      display: block;
      width: 100%;
      border: none;
    }

    .algolia-docsearch-suggestion--category-header-lvl0 {
      opacity: .6;
      font-size: 0.85em;
    }

    .algolia-docsearch-suggestion--category-header-lvl1 {
      opacity: .6;
      font-size: 0.85em;
      &:before {
        background-image: url('data:image/svg+xml;utf8,<svg width="10" height="10" viewBox="0 0 20 38" xmlns="http://www.w3.org/2000/svg"><path d="M1.49 4.31l14 16.126.002-2.624-14 16.074-1.314 1.51 3.017 2.626 1.313-1.508 14-16.075 1.142-1.313-1.14-1.313-14-16.125L3.2.18.18 2.8l1.31 1.51z" fill-rule="evenodd" fill="%231D3657" /></svg>');
        content: "";
        width: 10px;
        height: 10px;
        display: inline-block;
      }
    }

    .algolia-docsearch-suggestion--wrapper {
      width: 100%;
      float: left;
      margin: 0;
      padding: 0;
    }

    .algolia-docsearch-suggestion--duplicate-content, .algolia-docsearch-suggestion--subcategory-inline {
      display: none !important;
    }

    .algolia-docsearch-suggestion--title {
      margin: 0;
      color: #458ee1;
      font-size: .9em;
      font-weight: 400;
      &:before {
        content: "#";
        font-weight: 700;
        color: #458ee1;
        display: inline-block;
      }
    }

    .algolia-docsearch-suggestion--text {
      margin: 4px 0 0;
      display: block;
      line-height: 1.4em;
      padding: 5.33333px 8px;
      background: #f8f8f8;
      font-size: .85em;
      opacity: 0.8;

      .algolia-docsearch-suggestion--highlight {
        color: #3f4145;
        font-weight: 700;
        box-shadow: none;
      }
    }
  }

  .algolia-docsearch-footer {
    width: 134px;
    height: 20px;
    z-index: 2000;
    margin-top: 10.66667px;
    float: right;
    font-size: 0;
    line-height: 0;
  }

